import { Component, OnInit } from '@angular/core';
import { User } from '../user';
import { Patient } from '../patient';
import { Router, ActivatedRoute } from '@angular/router';
import { UserRegistationService } from '../user-registation.service';
import {LoginComponent} from '../login/login.component';

@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.css']
})
export class ViewComponent implements OnInit {

  users:any;
  user: User=new User("","","");
  message:any;
  name:any;
  patients:any;
  patient :Patient= new Patient(0,"","",0,"","");
  public count:number = 0;

  constructor(
    private router: Router,
    private service:UserRegistationService,
    private loginComponent: LoginComponent) { }

    
    ngOnInit() {
      console.log("count value "+ this.count);
        this.getView();
    }

    public viewPatient(patients){
      this.count=this.count+1;
      console.log("count value inside new view"+ this.count);
      console.log("logging values in view from search "+patients);
      this.router.navigate(['/view']);
    }
    public getViews(patientid,patientname,patientgender,patientage,patientdate){
      console.log("search page view "+ patientid+patientname+patientgender+patientage+patientdate);
      this.patients={id : patientid,name:patientname,gender:patientgender,age:patientage,patientdate:patientdate};
      console.log(this.patients);
      this.viewPatient(this.patients);
    }
  
    public getView(){
      console.log("I am here in view nginit")
      let resp=this.service.getView();
  resp.subscribe((data)=>{this.patients= data;
    console.log("logging data value"+data);
    console.log(this.patients);
    this.router.navigate(['/view']);
    });
    }
  

    public viewPatients(patients){
      this.patients=patients;
      
      console.log(this.patients);
      
    }

  public patientNow(){
let resp=this.service.doPrescription(this.patient);
resp.subscribe((data)=>this.message=data);

  }

  public logoutNow(){
    if(this.loginComponent.isUserLoggedIn()){
      this.loginComponent.logOut();
    let resp=this.service.doLogout();
    resp.subscribe((data)=>this.name= data);
     this.router.navigate(['/login']);
    }
    else{
      this.loginComponent.loginNow(); 
    }
    }

}
